@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: left;
  padding: 0vw;
}

.App-logo {
  /*float: left;
   padding-left: -50vmin;*/
  height: 8vmin;
  pointer-events: none;
 
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 5%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.body_container{
  padding: 1vw;
}
.note{
  color: green;
}

body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(to right, #e67e22, #e74c3c);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main {
  height: 90vh;
  background: #fff;
  box-shadow: 0px 13px 40px -13px rgba(0,0,0,0.75);
  width: 80%;
  margin: 0 auto;
}

.title-container {
  height: 90vh;
  background: linear-gradient(to right, #e67e22, #e74c3c);
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: 'Roboto Slab', serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: "Merriweather", serif;
}

.form-container {
  background-color: #2c3e50;
  /* height: 90vh; */
  padding-top: 15px;
  padding-left: 10px;
  border-radius: 15px;
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  width: 15%;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0px 0px 0px 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  border: 0;
  padding: 8px 20px;
  margin: 0 2px;
  border-radius: 2px;
  font-weight: lighter;
  letter-spacing: 1px;
  font-size: 15px;
  cursor: pointer;
  background-color: #f16051;
  color: #fff;
  font-weight: 100;
  border-radius: 15px;
}

button:active {
  outline:aliceblue;
}

.weather__info {
  width: 90%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
  -webkit-column-fill: balance;
          column-fill: balance;
  padding-top: 1vh;
  
}
.twoColumn {

  -webkit-column-count: 2;

          column-count: 2;
  -webkit-column-rule-style: solid;
          column-rule-style: solid;
  -webkit-column-rule-width: 1px;
          column-rule-width: 1px;
  -webkit-column-rule-color: lightblue;
          column-rule-color: lightblue;
}

.weather__key {
  color: #f16051;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}

.copyright {
  align-items: center;
  text-align: center;
  color: #ffffff;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 100;
  background: linear-gradient(to right, #e67e22, #e74c3c);
}

.valid {
  align-items: left;
  text-align: left;
  color: #ffffff;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 100;
}

a{
  color: cadetblue;
}
.footer_home {
  background-color: #282c34;
  min-height: 5%;
  display: flex;
  flex-direction: column;
  align-items: right;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  text-align: right;
  padding-right: 2vh;
  font-size: 2vh;
  padding-top: 0%;
}

.weather__city_info {
  color: #cdd8b2;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}
footer{
  padding-top: 1vh;
  margin-top: -1vh;
}
.todayDate{
  color: #b4f5d4;
}
.today{
  text-align: right;
  align-items: center;
  float: right;
  color: #ffffff;
  padding-right: 1vh;
}
.white{
  color: #ffffff;
}
.whitePadding{
  color: #ffffff;
  padding-left: 10vw;
}

.errorMsg{
  background-color: red;
  -webkit-animation-name: errortext;
          animation-name: errortext;
  -webkit-animation-duration: 30s;
          animation-duration: 30s;
  font-weight: bolder;
  border-radius: 8px;
}

@-webkit-keyframes errortext {
  0%   {background-color: red;}
  25%  {background-color: yellow;}
  50%  {background-color: blue;}
  100% {background-color: green;}
}

@keyframes errortext {
  0%   {background-color: red;}
  25%  {background-color: yellow;}
  50%  {background-color: blue;}
  100% {background-color: green;}
}

@media only screen and (max-width: 600px) {
  .smallScreen {
    display: none;
  }
}
